import { FirebaseService } from './../../services/firebase.service';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { AngularFireStorage } from '@angular/fire/storage';
import { finalize} from "rxjs/operators";

@Component({
  selector: 'app-addmeal',
  templateUrl: './addmeal.component.html',
  styleUrls: ['./addmeal.component.scss']
})
export class AddmealComponent implements OnInit {

  formTemplate= new FormGroup({
    foodName : new FormControl(''),
    foodDescription : new FormControl(),
    categoryName : new FormControl(''),
    foodPrice : new FormControl(''),
    imageUrl : new FormControl('')
  })

  constructor(public service : FirebaseService, public storage :AngularFireStorage) { }
  image : any= null;
  url : string;
  categorylist : any;
  ngOnInit() {

      this.resetForm();
      this.service.getCategory().subscribe(
        list=>{
          this.categorylist = list.map(item =>{
            return{
              ...item.payload.val()
            }
          })
        }
      )

  }

  //Displaying Image
  selectedImg(event :any)
  {
    if(event.target.files&& event.target.files[0])
    {
      const reader= new FileReader();


      reader.onload=(e:any)=> this.url= e.target.result;
      reader.readAsDataURL(event.target.files[0]);
      this.image = event.target.files[0];

       //this.image=  reader.readAsDataURL(e.target.files[0]);
    }
    else{
      this.url= "./assets/image-holder.png";
      this.image=null

    }


  }

  onSubmit(formValue)
  {
     var path = `Foodlist/ ${this.image.name.split('.').slice(0,-1).join('.')}_${new Date().getTime()}`;
    const fileRef = this.storage.ref(path)
     this.storage.upload(path,this.image).snapshotChanges().pipe(
       finalize(()=>{

        fileRef.getDownloadURL().subscribe((url)=>{
          formValue['imageUrl']= url;
          console.log(formValue)
         this.service.addFood(formValue)
          this.resetForm();

        })

       })
     ).subscribe();

  }

  resetForm()
  {
    this.formTemplate.reset();
    this.formTemplate.setValue({
      categoryName :'',
      foodName :'',
      foodDescription :'',
      foodPrice :'',
    imageUrl:''
    })
    this.image=null;
    this. url="./assets/image-holder.png"
  }

}
