import { Component, OnInit } from '@angular/core';
import { AngularFireStorage } from '@angular/fire/storage';
import { FormControl, FormGroup } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { FirebaseService } from 'src/app/services/firebase.service';

@Component({
  selector: 'app-addactivity',
  templateUrl: './addactivity.component.html',
  styleUrls: ['./addactivity.component.scss']
})
export class AddactivityComponent implements OnInit {


  formTemplate= new FormGroup({
    categoryName : new FormControl(''),
    imageUrl : new FormControl('')
  })
  constructor(private storage : AngularFireStorage, public service : FirebaseService) { }


  image : any= null;
url : string;
categorylist : any;

  ngOnInit(){


    this.resetForm();
    this.service.getActivity().subscribe(
      list=>{
        this.categorylist = list.map(item =>{
          return{
            ...item.payload.val()
          }
        })
      }
    )


  }


  selectedImg(event :any)
  {
    if(event.target.files&& event.target.files[0])
    {
      const reader= new FileReader();


      reader.onload=(e:any)=> this.url= e.target.result;
      reader.readAsDataURL(event.target.files[0]);
      this.image = event.target.files[0];

       //this.image=  reader.readAsDataURL(e.target.files[0]);
    }
    else{
      this.url= "./assets/image-holder.png";
      this.image=null

    }


  }

  onSubmit(formValue)
  {
     var path = `Activity/ ${this.image.name.split('.').slice(0,-1).join('.')}_${new Date().getTime()}`;
    const fileRef = this.storage.ref(path)
     this.storage.upload(path,this.image).snapshotChanges().pipe(
       finalize(()=>{

        fileRef.getDownloadURL().subscribe((url)=>{
          formValue['imageUrl']= url;
          console.log(formValue)
         this.service.addActivity(formValue)
          this.resetForm();

        })

       })
     ).subscribe();

  }
  resetForm()
  {
    this.formTemplate.reset();
    this.formTemplate.setValue({
      categoryName :'',
    imageUrl:''
    })
    this.image=null;
    this. url="./assets/image-holder.png"
  }

}
