import { FirebaseService } from './../../services/firebase.service';
import { Component, OnInit } from '@angular/core';
import { AngularFireStorage } from '@angular/fire/storage';
import { FormControl, FormGroup } from '@angular/forms';
import { finalize} from "rxjs/operators"

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.scss']
})
export class CategoryComponent implements OnInit {

  formTemplate= new FormGroup({
    categoryName : new FormControl(''),
    imageUrl : new FormControl('')
  })

  constructor( private storage : AngularFireStorage, public service : FirebaseService) { }
image : any= null;
url : string;
categorylist : any;
  ngOnInit() {

    this.resetForm();
    this.service.getCategory().subscribe(
      list=>{
        this.categorylist = list.map(item =>{
          return{

            $key : item.key,
            ...item.payload.val()
          }
        })
      }
    )

  }

  remove(key)
  {

  this.service.deleteCategory(key)
  }


  selectedImg(event :any)
  {
    if(event.target.files&& event.target.files[0])
    {
      const reader= new FileReader();


      reader.onload=(e:any)=> this.url= e.target.result;
      reader.readAsDataURL(event.target.files[0]);
      this.image = event.target.files[0];

       //this.image=  reader.readAsDataURL(e.target.files[0]);
    }
    else{
      this.url= "./assets/image-holder.png";
      this.image=null

    }


  }

  onSubmit(formValue)
  {
     var path = `CategoryList/ ${this.image.name.split('.').slice(0,-1).join('.')}_${new Date().getTime()}`;
    const fileRef = this.storage.ref(path)
     this.storage.upload(path,this.image).snapshotChanges().pipe(
       finalize(()=>{

        fileRef.getDownloadURL().subscribe((url)=>{
          formValue['imageUrl']= url;
          console.log(formValue)
         this.service.addCategory(formValue)
          this.resetForm();

        })

       })
     ).subscribe();

  }
  resetForm()
  {
    this.formTemplate.reset();
    this.formTemplate.setValue({
      categoryName :'',
    imageUrl:''
    })
    this.image=null;
    this. url="./assets/image-holder.png"
  }
}
