import { OdialogComponent } from './../odialog/odialog.component';
import { FirebaseService } from 'src/app/services/firebase.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.scss']
})
export class OrdersComponent implements OnInit {

  constructor( public service :FirebaseService, private dailog :MatDialog) { }


  ListData : MatTableDataSource<any>;
  displayedColumns: String[] = ['Room Number','Names','Status','Total','Action']
  orders : any;
  @ViewChild(MatSort) sort : MatSort;
  @ViewChild(MatPaginator) paginator : MatPaginator;
  searchKey : string;

  ngOnInit(){


    this.service.getOrders().subscribe(
      list=>{
        let array = list.map(item =>{
          return{
            $key : item.key,
            ...item.payload.val()
          };


        });

        this.ListData = new MatTableDataSource(array);
        this.ListData.paginator= this.paginator;
      });





  }

  clearSearch()
  {
    this.searchKey="";
  }

  Search()
  {
    this.ListData.filter =this.searchKey.trim().toLocaleLowerCase();
  }

  update(k)
  {
      this.service.populateOrder(k);
      this.dailog.open(OdialogComponent)

  }
  remove(key)
  {
    if(confirm('Are you sure u wanna delete'))
    {



  this.service.deleteOrder(key)
    }




  }




  passdata(total,foood,names,s,accomo)
  {
     this.service.getorders(total,foood,names,s,accomo);
  }

}
