import { Component, OnInit } from '@angular/core';
import { AngularFireStorage } from '@angular/fire/storage';
import { FormControl, FormGroup } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { FirebaseService } from 'src/app/services/firebase.service';

@Component({
  selector: 'app-add-accomo',
  templateUrl: './add-accomo.component.html',
  styleUrls: ['./add-accomo.component.scss']
})
export class AddAccomoComponent implements OnInit {

  formTemplate= new FormGroup({
    AccomName : new FormControl(''),
    BedType : new FormControl(),
    Rooms : new FormControl(),
    AccomDescription : new  FormControl(),
    AccomPrice : new FormControl(''),
    imageUrl : new FormControl('')
  })

  constructor( public service : FirebaseService, public storage :AngularFireStorage) { }
  image : any= null;
  url : string;
  categorylist : any;



  ngOnInit() {

    this.resetForm();
    this.service.getCategory().subscribe(
      list=>{
        this.categorylist = list.map(item =>{
          return{
            ...item.payload.val()
          }
        })
      }
    )


  }


 //Displaying Image
 selectedImg(event :any)
 {
   if(event.target.files&& event.target.files[0])
   {
     const reader= new FileReader();


     reader.onload=(e:any)=> this.url= e.target.result;
     reader.readAsDataURL(event.target.files[0]);
     this.image = event.target.files[0];

      //this.image=  reader.readAsDataURL(e.target.files[0]);
   }
   else{
     this.url= "./assets/image-holder.png";
     this.image=null

   }


 }




  onSubmit(formValue)
  {
     var path = `Acccomodation/ ${this.image.name.split('.').slice(0,-1).join('.')}_${new Date().getTime()}`;
    const fileRef = this.storage.ref(path)
     this.storage.upload(path,this.image).snapshotChanges().pipe(
       finalize(()=>{

        fileRef.getDownloadURL().subscribe((url)=>{
          formValue['imageUrl']= url;
          console.log(formValue)
         this.service.addAccomodation(formValue)
          this.resetForm();


        })

       })
     ).subscribe();

  }




  resetForm()
  {
    this.formTemplate.reset();
    this.formTemplate.setValue({
      BedType :'',
      AccomName :'',
      AccomDescription :'',
      Rooms :'',
      AccomPrice :'',
    imageUrl:''
    })
    this.image=null;
    this.url="./assets/image-holder.png"
  }

}
