import { AddactivityComponent } from './Admin/addactivity/addactivity.component';
import { OrdersComponent } from './Admin/orders/orders.component';
import { ViewFoodComponent } from './Admin/view-food/view-food.component';
import { BookingsComponent } from './Admin/bookings/bookings.component';
import { AddmealComponent } from './Admin/addmeal/addmeal.component';
import { CategoryComponent } from './Admin/category/category.component';
import { TestComponent } from './pages/test/test.component';
import { SignInComponent } from './pages/sign-in/sign-in.component';
import { ClientsComponent } from './Admin/clients/clients.component';
import { DashboardComponent } from './Admin/dashboard/dashboard.component';
import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DefaultComponent } from './pages/default/default.component';
import { AddAccomoComponent } from './Admin/add-accomo/add-accomo.component';
import { ViewAccomoComponent } from './Admin/view-accomo/view-accomo.component';
import { OrderDetailsComponent } from './Admin/order-details/order-details.component';

const routes: Routes = [

  {path:'signin', component: SignInComponent},
  {path:'test', component: TestComponent},
  { path:'', component: DefaultComponent,
    children:[{path:'', component:DashboardComponent},
    {path:'clients', component:ClientsComponent},
    {path:'gategory', component :CategoryComponent},
    {path: 'addmeal', component: AddmealComponent},
    {path: 'addAccomodation' , component: AddAccomoComponent},
    {path: 'Bookings', component: BookingsComponent},
    {path:'Viewfood', component: ViewFoodComponent},
    {path:'View Accomodation', component: ViewAccomoComponent},
    {path: 'Orders', component: OrdersComponent},
    {path:'Activities', component: AddactivityComponent},
    {path:'Order/:ref', component: OrderDetailsComponent},
  ],
},


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
