import { BDialogComponent } from './../b-dialog/b-dialog.component';
import { CategoryComponent } from './../category/category.component';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { FirebaseService } from 'src/app/services/firebase.service';
import { ToastrService } from 'ngx-toastr';



@Component({
  selector: 'app-bookings',
  templateUrl: './bookings.component.html',
  styleUrls: ['./bookings.component.scss']
})
export class BookingsComponent implements OnInit {

  constructor(public service : FirebaseService, private dailog :MatDialog, public toastr :ToastrService) { }

  ListData : MatTableDataSource<any>;
  displayedColumns: String[] = ['Names','Room Name','Room Number','Check In','Check Out','Amount','Status','Action']
  Reserve : any;
  @ViewChild(MatSort) sort : MatSort;
  @ViewChild(MatPaginator) paginator : MatPaginator;
  searchKey : string;



  ngOnInit() {

    this.service.getBookings().subscribe(
      list=>{
        let array = list.map(item =>{
          return{
            $key : item.key,
            ...item.payload.val()
          };


        });

        this.ListData = new MatTableDataSource(array);
        this.ListData.paginator= this.paginator;
      });


  }

  clearSearch()
  {
    this.searchKey="";
  }

  Search()
  {
    this.ListData.filter =this.searchKey.trim().toLocaleLowerCase();
  }

  update(k)
  {
    this.dailog.open(BDialogComponent);
    this.service.populate(k)
    console.log("key"+k)
  }

  remove(key)
  {
    if(confirm('Are you sure u wanna delete'))
    {



  this.service.deleteBookings(key)
    }
  }



}
